<template>
  
    <div id="home">

      <NavBar class="home-nav"><div slot="center"></div></NavBar>
      <TabControl :titles="['','','']" 
                  class="tab-control1"
                   @tabClick="tabClick"
                   ref = "tabControl1" v-show ="isTabFixed"/>

      <Scroll class="content" 
              ref="scroll" 
              :probe-type="3" 
              @scroll="contentScroll" 
              :pullUpLoad="true"
              @pullingUp = "loadMore">
              <HomeSwiper :banners="banners" @swiperImageLoad="swiperImageLoad"/>
              <RecommendView :recommends="recommends"/>
              <FeatureView/>
              <TabControl :titles="['','','']"                 
                   @tabClick="tabClick"
                   ref = "tabControl2" />
              <GoodsList :goods="showGoods"/>
      </Scroll>
     
      <BackTop @click.native="backClick" v-show="isShowBackTop"/>
  

    </div>

</template>

<script>
//
import HomeSwiper from "views/home/childComps/HomeSwiper.vue"
import RecommendView from "views/home/childComps/RecommendView.vue" 
import FeatureView from "views/home/childComps/FeatureView.vue"

//
import NavBar from "@/components/common/navbar/NavBar.vue"
import TabControl from "@/components/content/tabControl/TabControl.vue"
import GoodsList from "@/components/content/goods/GoodsList.vue"
import Scroll from '@/components/common/scroll/Scroll.vue'
import BackTop from '@/components/content/backTop/BackTop.vue'
//
import {getHomeMultidata,getHomeGoods} from "network/home.js"
import {debounce} from "@/common/utils.js"


export default {
  name: 'Home',
  components:{
    HomeSwiper,
    RecommendView,
    FeatureView,
    NavBar,
    TabControl,
    GoodsList,
    Scroll,
    BackTop

  },
  data(){
    return{
      banners:[],
      recommends:[],
      goods:{
        "pop":{page:0,list:[]},
        "new":{page:0,list:[]},
        "sell":{page:0,list:[]},
      },
      currentType:"pop",
      isShowBackTop:false,
      tabOffsetTop:0,
      isTabFixed:false
    }
  },
  computed:{
    showGoods(){
      return this.goods[this.currentType].list
    }
  },
  //
  //api),created()
  created(){
    //1.
      this.getHomeMultidata()

    //2.
    this.getHomeGoods("pop")
    this.getHomeGoods("new")
    this.getHomeGoods("sell")

   
  },

  //created?
  //mounted
  mounted(){
    const refresh = debounce(this.$refs.scroll.refresh,50)
   
    //1.item
    this.$bus.$on('itemImageLoad',()=>{
      
      //this.$refs.scroll.refresh()
      //console.log("---");
      refresh() 
    })
    //2.tabControloffsetTop
    //$el:
    //this.tabOffsetTop = this.$refs.tabControl.$el.offsetTop
    //console.log(this.$refs.tabControl);
    //console.log(this.$refs.tabControl.$el);
    //console.log(this.$refs.tabControl.$el.offsetTop);
    
  },
  methods:{
    //

    //tabcontrol
    tabClick(index){
      //console.log(index);
      switch(index){
        case 0:
          this.currentType = "pop"
          break
        case 1:
          this.currentType = "new"
          break
        case 2:
          this.currentType = "sell"
      }
      this.$refs.tabControl1.currentIndex = index;
      this.$refs.tabControl2.currentIndex = index;
    },
    backClick(){
      //.native
      //.native
      console.log("native");
      this.$refs.scroll.scrollTo(0,0,500)
    },
    //
    contentScroll(position){
      //console.log(position);
      //yy
      //1.backtop
      this.isShowBackTop = position.y < -1000

      //2.tabControl(position:fixed)
      this.isTabFixed = position.y < -this.tabOffsetTop
      //console.log(1);
    },
    loadMore(){
      //console.log("loadmoresuccess");
      this.getHomeGoods(this.currentType)
      //refresh()scrollhieght,
      this.$refs.scroll.scroll.refresh()
    },
    swiperImageLoad(){
      //console.log("--swiperImageLoad--");
      //console.log(this.$refs.tabControl.$el.offsetTop);
      //console.log(this.tabOffsetTop);
      this.tabOffsetTop = this.$refs.tabControl2.$el.offsetTop
      //console.log(this.tabOffsetTop);
    },
    //
    getHomeMultidata(){
        getHomeMultidata().then(res=>{
        //console.log(res);
        this.banners = res.data.data.banner.list;
        this.recommends = res.data.data.recommend.list;
        })
    },
    getHomeGoods(type){
        const page= this.goods[type].page + 1;
        getHomeGoods(type,page).then(res=>{
          //console.log(res);
          this.goods[type].list.push(...res.data.data.list)
          this.goods[type].page+=1
          this.$refs.scroll.finishPullUp()             
        }) 
    }
  }


}
</script>

<style scoped>

#home{
  //background-color:pink;
 
  position: relative;
  left:0;
  right:0;
  top:0;
  //padding-top:44px;
  height:100vh;
 
  
}

.home-nav{
background-color:#ff8198;
color:#fff;
  /*,*/
  //position:fixed;
  //left:0;
  //right:0;
  //top:0;
  //z-index:9;
}

.tab-control1{
  position: relative;;
  //top:44px;
  background-color:#fff;
  z-index: 20;
}
 
.content{
  
  overflow: hidden;
  position:absolute;
  top:44px;
  bottom:98px;
  left:0;
  right:0; 
}


</style>